.wrapper{
    -webkit-tap-highlight-color: transparent;
    position: fixed;
    top: -200%;
    /*left: -100%;*/
    /* right: 0; */
    height: 100%;
    width: 100%;
    background-color: rgb(211, 211, 211);
    /*background: linear-gradient(90deg, #f92c78, #4114a1);*/
    /* background: linear-gradient(375deg, #1cc7d0, #2ede98); */
   /* background: linear-gradient(-45deg, #e3eefe 0%, #efddfb 100%);*/
    transition: all 0.4s ease-in-out;
  }
  .costam{
    -webkit-tap-highlight-color: transparent;
    z-index: 5;
    position: absolute;
  }
  #active:checked ~ .wrapper{
    -webkit-tap-highlight-color: transparent;
    /*left: 0;*/
    /* right:0; */
    top: 0;
  }
  .menu-btn{
    -webkit-tap-highlight-color: transparent;
    position: absolute;
    /* top: 0; */
    z-index: 2;
    right: 20px;
    /*left: 20px; */
    top: 12px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;
    font-size: 20px;
    cursor: pointer;
    /*color: #fff;*/
    /*background: linear-gradient(90deg, #f92c78, #4114a1);*/
    /* background: linear-gradient(375deg, #1cc7d0, #2ede98); */
   /* background: linear-gradient(-45deg, #e3eefe 0%, #efddfb 100%); */
    transition: all 0.3s ease-in-out;
  }
  .menu-btn span,
  .menu-btn:before,
  .menu-btn:after{
      content: "";
      position: absolute;
      top: calc(50% - 1px);
      left: 30%;
      width: 40%;
      border-bottom: 2px solid #000;
      transition: transform .6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  .menu-btn:before{
    transform: translateY(-8px);
  }
  .menu-btn:after{
    transform: translateY(8px);
  }
  
  .close {
      z-index: 1;
      width: 100%;
      height: 100%;
      pointer-events: none;
      transition: background .6s;
  }
  
  /* closing animation */
  #active:checked + .menu-btn span {
      transform: scaleX(0);
  }
  #active:checked + .menu-btn:before {
      transform: rotate(45deg);
    border-color: rgb(0, 0, 0);
  }
  #active:checked + .menu-btn:after {
      transform: rotate(-45deg);
    border-color: rgb(0, 0, 0);
  }
  .wrapper ul{
    position: absolute;
    top: 40%;
    left: 50%;
    height: 100%;
    transform: translate(-54%, -50%);
    list-style: none;
    text-align: center;
  }
  .wrapper ul li{
    height: 10%;
    margin: 15px 0;
  }
  .wrapper ul li a{
    text-decoration: none;
    font-size: 30px;
    font-weight: 500;
    padding: 5px 30px;
    color: rgb(0, 0, 0);
    border-radius: 50px;
    /* position: absolute; */
    line-height: 50px;
    margin: 5px 30px;
    opacity: 0;
    /* transition: all 0.3s ease; 
     transition: transform .6s cubic-bezier(0.215, 0.61, 0.355, 1);  */
  }
